<template>
  <div class="sign sign-in">
    <div class="sign__header clearfix">
      <i @click="$router.replace('/')" class="sign__back"></i>
      <button @click="$router.replace('/register')" class="sign__button">注册</button>
    </div>
    <h1 class="sign__title">登录</h1>
    <form @submit.prevent class="form">
      <div class="form__item">
        <label class="form__label" for="sign_in_acc">
          手机号／用户名
        </label>
        <div class="form__content form__content--between">
          <input v-model.trim="loginForm.loginName" class="form__input form__input--limit-width sign-in-form__input "
                 id="sign_in_acc"
                 maxlength="12" type="text">
          <SeedCode @sendCodeBefore="sendCodeBefore"
                    @sendCallBack="sendCallBack"
                    :params="sendCodeParams"
                    :enable="enableSendBtn"
                    class="form__button--suffix">
          </SeedCode>
        </div>
      </div>
      <div class="form__item">
        <label class="form__label" for="sign_in_pwd">
          验证码／密码
          <a class="form__link-service" href="javascript:void (0);">收不到验证码？</a>
        </label>
        <div class="form__content">
          <input v-model.trim="loginForm.verifyStr" class="form__input sign-in-form__input"
                 id="sign_in_pwd" maxlength="16"
                 type="password">
        </div>
      </div>
      <div v-if="isNeedCaptcha" class="form__item">
        <label class="form__label" for="sign_in_code">图形验证码</label>
        <div class="form__content form__content--between">
          <input v-model.trim="loginForm.captcha"
                 @input="replaceNonNum"
                 class="form__input form__input--limit-width sign-in-form__input" id="sign_in_code"
                 maxlength="4" type="text">
          <img @click="getCaptcha" class="form__captcha" :src="image" alt="">
        </div>
      </div>
      <button @click="submit" :class="{'disabled-opacity':!validateForm}" class="form__button form__button--full">登录
      </button>
    </form>
    <p class="sign__link-forget-password">
      <a href="javascript:void (0)" class="link">忘记账号／密码？</a>
    </p>
    <p class="sign__notes">
      凯发娱乐支持USDT <span class="sign__notes-strong">安全 • 方便</span> 匿名买卖、不受银行监管
    </p>
  </div>
</template>

<script>
  import { mapMutations, mapActions } from 'vuex'
  import SeedCode from '@/components/SeedCode.vue'
  import {
    validateLoginName,
    validatePhoneNum,
    validatePassword
  } from '@/utils/validate.js'

  export default {
    name: 'sign_in',
    components: { SeedCode },
    beforeRouteEnter(to, from, next) {
      next(vm => {
        vm.formPath = from.path === '/register' ? '/' : from.fullPath
      })
    },
    data() {
      return {
        formPath: '/',
        loginForm: {
          loginName: null,
          verifyStr: null,
          messageId: null,
          captcha: null,
          captchaId: null
        },
        image: null,
        isNeedCaptcha: false,
        sendCodeParams: {
          use: 2,
          mobileNo: null
        }
      }
    },
    computed: {
      enableSendBtn() {
        return validatePhoneNum(this.loginForm.loginName)
      },
      validateForm() {
        if (this.enableSendBtn) {
          return this.loginForm.messageId &&
            this.loginForm.verifyStr &&
            this.loginForm.verifyStr.length === 6
        } else {
          return validateLoginName(this.loginForm.loginName) &&
            validatePassword(this.loginForm.verifyStr)
        }
      }
    },
    created() {
      this.getIsNeedCaptcha()
    },
    methods: {
      ...mapMutations(['setCustomerInfo']),
      ...mapActions([
        'login',
        'getCustomerInfo',
        'getBalanceInfo'
      ]),
      getIsNeedCaptcha() {
        let params = {
          _isNeedLoading: false
        }
        this.$api.getIsNeedCaptcha(params).then(({ head, body }) => {
          if (head['errCode'] === '0000') {
            this.isNeedCaptcha = !!body['needCaptcha']
            if (this.isNeedCaptcha) this.getCaptcha()
          }
        })
      },
      getCaptcha() {
        let params = {
          _isNeedLoading: false
        }
        this.$api.getCaptcha(params).then(({ head, body }) => {
          if (head['errCode'] === '0000') {
            this.loginForm.captchaId = body.captchaId
            this.image = 'data:image/jpeg;base64,' + body.image
          }
        })
      },
      replaceNonNum(e) {
        e.target.value = e.target.value.replace(/[^0-9]/ig, '')
      },
      sendCodeBefore() {
        this.sendCodeParams.mobileNo = this.$encrypted(this.loginForm.loginName)
      },
      sendCallBack(messageId) {
        this.loginForm.messageId = messageId
      },
      submit() {
        if (this.validateForm) {
          this.login(this.loginForm).then(({ head, body }) => {
            if (head['errCode'] === '0000') {
              this.$toast.success({
                message: '登录成功',
                callback: () => {
                  this.$router.replace(this.formPath)
                }
              })
            } else {
              this.getIsNeedCaptcha()
            }
          })
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "sign.scss";

  .sign-in-form {
    &__input {
      font-size: rem(24);
    }
  }

  .text {
    flex-grow: 1;
  }
</style>
