const loginName = /^[cC]\w{4,10}/
const phoneNum = /^1\d{10,11}$/
const password = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,16}$/
const parentId = /^\d{10}/

export function validateLoginName(str) {
  return loginName.test(str)
}

export function validatePhoneNum(str) {
  return phoneNum.test(str)
}

export function validatePassword(str) {
  return password.test(str)
}

export function validateParentId(str) {
  return parentId.test(str)
}

export default {
  validateLoginName,
  validatePhoneNum,
  validatePassword,
  validateParentId
}
